.print-drawing {
  font-family: "Futura PT";
  position: relative;
  background-color: #f6f6f6;
  background-image: url("../img/print-drawing-img.jpg");
  background-position: left 50%;
  background-repeat: no-repeat;
  background-size: contain;
  height: 578px;

  @media (max-width: 600px) {
    background-image: none;
    height: 400px;

  }

  &::before {
    position: absolute;
    content: "";
    top: 102px;
    right: 3%;
    width: 255px;
    height: 375px;
    background-image: url("../img/group-point.svg");
    background-position: 50% 0;
    background-repeat: no-repeat;

    @media (max-width: 1800px) {
      display: none;
    }
  }
}

